.App {
  text-align: center;
  background-color: #ced6e0;
  color: #2f3542;
  min-height: 2000px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #eab543;
  min-height: 50vh;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
}

.App-link {
  color: #61dafb;
}

.photo-container {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(auto-fill, 350px);
  grid-row-gap: 25px;
  /* flex-direction: column;
  justify-content: space-around;
  width: 100%; */
}

